import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { BandejaTemporadaComponent } from './hotel/temporada/bandeja-temporada/bandeja-temporada.component';
import { BandejaHabitacionComponent } from './hotel/habitacion/bandeja-habitacion/bandeja-habitacion.component';

const routes: Routes = [
  { path: 'Teemporada', component: BandejaTemporadaComponent },
  { path: 'Haabitacion', component: BandejaHabitacionComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
