import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class HotelService {

  constructor(private http: HttpClient) { }

  listarTemporadas(params) {
    return this.http.post(environment.baseURL_ + "/Hotel/Temporada/ListarTemporada", params);
  }

  registrarTemporadas(params: any) {
    return this.http.post(environment.baseURL_ + "/Hotel/Temporada/Index", params);
  }

}
